		uni.showLoading({
			title: 'Loding...',
		});

		var userInfo = this.getGlobalUserInfo();
		if (userInfo == null || userInfo == undefined || userInfo == "") {
			uni.navigateTo({
				url: '../login/login',
			});
			return;
		}

		console.log(userInfo);
		this.queryUserInfo(userInfo);
	},
	methods: {
		upload() {
			var userInfo = this.getGlobalUserInfo();
			if (userInfo == null || userInfo == undefined || userInfo == "") {
				uni.navigateTo({
					url: '../login/login',
				});
				return;
			}

			uni.chooseImage({
				count: 1, // 默认9
				sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success(res) {
					const src = res.tempFilePaths[0];
					console.log("src=" + src)
					uni.redirectTo({
						url: '../upload/upload?src=' + src
					});
				}
			});
		},

		// 用户注销，清楚用户缓存
		logout: function() {
			var userInfo = this.getGlobalUserInfo();
			if (userInfo == null || userInfo == undefined || userInfo == "") {
				uni.navigateTo({
					url: '../login/login',
				});
				return;
			}
			var that = this;
			uni.request({
				url: that.$serverUrl + '/logout?userId' + userInfo.id,
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					console.log(res.data);
					var status = res.data.status;
					if (status == 200) {
						uni.showToast({
							icon: 'none',
							title: 'Logout'
						});
						that.removeGlobalUserInfo();
						console.log("logout: userInfo.id=" + that.getGlobalUserInfo.id);
						console.log("logout: userInfo.userToken=" + that.getGlobalUserInfo.userToken);
						uni.navigateTo({
							url: '../login/login',
						});
					} else if (status == 500) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
						});
					}
				},
			});
		},

		queryUserInfo(userInfo) {
			var that = this;
			uni.request({
				url: that.$serverUrl + '/user/query?userId=' + userInfo.id,
				method: 'POST',
				header: {
					'content-type': 'application/json',
					'userId': userInfo.id,
					'userToken': userInfo.userToken,
				},
				success: (res) => {
					console.log(res.data);
					var status = res.data.status;
					if (status == 200) {
						uni.hideLoading();
						// 赋值到当前页面
						var userInfo = res.data.data;
						if (userInfo.faceImage != null) {
							that.faceUrl = that.$serverUrl + userInfo.faceImage;
						}
						// 加上user的其他属性

					} else if (status == 502) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
						});
					}
				}
			});
		}

